@import url('/src/components/Header/HeaderClass.scss');
@import url('/src/components/Login/LoginStyle.scss');

$main-color-black: #121417;
$dark-yellow: #f4c550;
$light-yellow: #fbe9ba;

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Roboto', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.container {
  max-width: 320px;
  width: 100%;
  padding: 0 16px;
  margin-left: auto;
  margin-right: auto;

  @media screen and (min-width: 768px) {
    padding: 0 64px;
    max-width: 768px;
  }
  @media screen and (min-width: 1440px) {
    max-width: 1440px;
  }
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}

p {
  margin: 0;
}

a {
  text-decoration: none;
  color: $main-color-black;
}

button {
  cursor: pointer;
}

ul {
  list-style: none;
}
